	export default {
		data() {
			return {
			  api: this.GLOBAL.baseURL,
				bringData:1,
				bringArr:[
				  {label:'参与者不需要携带任何东西',value:1},
				  {label:'参与者需要东西',value:2},
				  ],
        needThing:'',
        rplayThing:'',
        complete:'',
        active_id:''
			};
		},
		methods:{
			changeRouter(){
			  if(this.bringData > 1) {
          if (!this.needThing) {
            this.$message({type: 'info', message: '请填写参与者需要带的物品'})
          }else{
            this.save()
          }
        }else{
          this.save()
          }
        },
      save(){
        if(this.active_id){
          this.$http.post(this.api + '/home/Activity/save_activity',{
            token: localStorage.getItem('token'),
            activity_id: this.active_id,
            activ_bring: this.needThing,
            step:6
          })
            .then(res=>{
              if(res.data.code == 1){
                if(!this.complete){
                  this.$emit('changeRouter',{id:7,router:"Title",information: this.active_id,complete: this.complete})
                }else{
                  this.$message({
                    type: 'success',
                    message: '保存成功！'
                  })
                }
              }else if(res.data.code == 3){
                this.save()
              }else if(res.data.code == 0){
                this.$alert(res.data.msg)
              }
            })
        }else {
          this.$http.post(this.api + '/home/Activity/save_activity', {
            token: localStorage.getItem('token'),
            activ_bring: this.needThing,
            step: 6
          })
            .then(res => {
              if (res.data.code == 1) {
                this.$emit('saveId',res.data.data)
                this.$emit('changeRouter', {id: 7, router: "Title",information:res.data.data})
              } else if (res.data.code == 3) {
                this.save()
              } else if(res.data.code == 0){
                this.$alert(res.data.msg)
              }
            })
        }
      },
      changeTxt(){
			  if(this.bringData == 1){
          this.rplayThing = this.needThing
          this.needThing = ''
        }else{
          this.needThing =  this.rplayThing
        }
      },
      getActives(){
        this.$http.post(this.api + '/ActivityE',{
          token: localStorage.getItem('token'),
          activity_id: this.active_id,
          visit: 0
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.activ_bring){
                this.bringData = 2
                this.needThing = res.data.data.activ_bring
              }
            }else if(res.data.code == 3){
              this.getActives()
            }else if(res.data.code == 0){
              this.$alert(res.data.msg)
            }
          })
      }
		},
		created(){
      window.scrollTo(0,0);
      this.complete = this.$route.query.complete
      this.active_id = this.$route.query.information
		},
    mounted() {
      window.scroll(0,0)
		  let _this = this
      if(_this.active_id){
        _this.getActives()
      }
    }
  }



